// Body
$body-bg: #f8fafc;

// Typography
$font-family-sans-serif: 'Nunito', sans-serif;
$font-size-base: 0.9rem;
$line-height-base: 1.6;

// Colors
$blue: #3490dc;
$indigo: #6574cd;
$purple: #9561e2;
$pink: #f66d9b;
$red: #e3342f;
$orange: #f6993f;
$yellow: #ffed4a;
$green: #6aab35;
$teal: #4dc0b5;
$cyan: #6cb2eb;

$theme-colors: (
    "primary": #6aab35,
    "secondary": #184399,
    "danger": #e3342f,
    "warning": #ff9800,
    "dark": #263238,
    "info": #6cb2eb,
    "success": #689f38,
    "light": #ffffff,
);